body {
	background: #e9ecee;

	main {
		padding: 60px 0 0 280px;
	}

	&.active {
		main {
			padding   : 60px 0 0 80px;
			transition: .2s all ease-in-out;
		}

		header {
			left: 80px;
		}
	}
}

.ofc {
	width          : 100%;
	height         : 100%;
	object-fit     : cover;
	object-position: center;
	font-family    : 'object-fit: cover, object-position: center';
}

// THANH ĐƯỜNG DẪN TRANG
#breadcrumb-wrapper {
	padding-top: 65px;

	h1 {
		font-size  : 42px;
		color      : #434343;
		font-weight: 700;
		text-align : center;
	}

	.breadcrumb {
		list-style     : none;
		display        : flex;
		justify-content: center;
		padding        : 0;

		li {
			a {
				color      : #000000;
				font-size  : 17px;
				display    : inline-block;
				padding-top: 20px;
			}

			&.active {
				a {
					color: red;
				}
			}

			&+li {
				&:before {
					content : "/";
					position: relative;
					left    : 0;
					top     : 0;
					margin  : 0 12px;
				}
			}
		}
	}

	@media (max-width: 767.98px) {
		.breadcrumb {
			li {
				a {
					font-size: 12px;
					padding  : 10px 0;
				}
			}
		}
	}
}

// CÁC NÚT PHÂN TRANG
.pagination {
	background     : #FFFFFF;
	display        : flex;
	justify-content: flex-end;
	padding        : 30px 0;

	@media (max-width: 1024.98px) {
		justify-content: center;
	}

	li {
		margin: 0 10px;

		@media (max-width: 1024.98px) {
			margin: 0 5px;
		}

		a {
			display        : flex;
			justify-content: center;
			align-items    : center;
			height         : 30px;
			width          : 30px;
			color          : #000000;
			border-radius  : 5px;
			font-weight    : 400;
			font-size      : 12px;
			background     : #dddddd;
			border         : 1px solid #FFFFFF;
			transition     : all .2s cubic-bezier(.2, .5, .5, .8);
		}

		&:hover {
			a {
				color     : #FFFFFF;
				background: red;
			}
		}

		&:last-child {
			margin: 0 0 0 10px;
		}

		&.active {
			a {
				color          : #FFFFFF;
				background     : red;
				text-decoration: underline;
			}
		}

		&.pagination-dot {
			a {
				border: 0;
			}
		}
	}
}